import React, { useState } from 'react';
import { Box, Container, Typography, Grid, Card, CardContent, Button, Fade, Paper } from '@mui/material';
import course1 from '../assets/images/Courses1.webp';
import course2 from '../assets/images/Courses2.webp';
import course3 from '../assets/images/Courses3.webp';
import course4 from '../assets/images/Courses4.webp';
import course5 from '../assets/images/Courses5.webp';

const courses = [
  {
    image: course1,
    title: 'Business Chinese',
    description: 'HSK/BCT graded Chinese, business news intensive reading, business etiquette culture, etc.',
    color: '#07284b',
    pricing: {
      online: '$80',
      offline: '$100',
      duration: '60 minutes',
      groupSize: '1 person',
      note: 'Small group: $50/person (2-4 people)'
    }
  },
  {
    image: course2,
    title: 'Exam Preparation',
    description: 'PSLE, O level (SEC from 2027), A level, IGCSE Chinese, IB Chinese, AP Chinese, OPI and other standardized exam tutoring.',
    color: '#9f2122',
    pricing: {
      online: '$80+',
      offline: '$100+',
      duration: '90 minutes',
      groupSize: '1-4 people',
      note: 'Group discounts available'
    }
  },
  {
    image: course3,
    title: 'Speech & Eloquence',
    description: 'Mandarin basics, children\'s recitation and drama, youth speech and eloquence.',
    color: '#07284b',
    pricing: {
      online: '$80',
      offline: '$80',
      duration: '60 minutes',
      groupSize: '1-4 people',
      note: 'Starting from $80/person'
    }
  },
  {
    image: course4,
    title: 'Immersion Programs',
    description: 'Holiday Program (3 hours Language + 3 hours culture/day)',
    color: '#9f2122',
    pricing: {
      online: '',
      offline: '$300/day',
      duration: '6 hours',
      groupSize: '2-6 people',
      note: 'Intensive cultural workshops included'
    }
  },
  {
    image: course5,
    title: 'Private Customization',
    description: 'Personalized courses tailored to individual needs and learning goals.',
    color: '#07284b',
    pricing: {
      online: 'Custom',
      offline: 'Custom',
      duration: 'Flexible',
      groupSize: 'Individual/Group',
      note: 'Contact for pricing'
    }
  }
];

function Courses() {
  const [hoveredCard, setHoveredCard] = useState(null);

  return (
    <Box
      sx={{
        py: 12,
        background: 'linear-gradient(45deg, rgba(135, 206, 250, 0.1) 0%, rgba(255, 182, 193, 0.08) 100%)',
        position: 'relative',
        '&::before': {
          content: '""',
          position: 'absolute',
          top: '10%',
          right: '5%',
          width: '120px',
          height: '120px',
          borderRadius: '50%',
          background: 'rgba(255, 182, 193, 0.15)',
          zIndex: 0
        },
        '&::after': {
          content: '""',
          position: 'absolute',
          bottom: '15%',
          left: '8%',
          width: '80px',
          height: '80px',
          borderRadius: '50%',
          background: 'rgba(255, 182, 193, 0.12)',
          zIndex: 0
        }
      }}
    >
      <Container maxWidth="xl" sx={{ px: { xs: 2, md: 4 }, position: 'relative', zIndex: 1 }}>
        {/* 标题区域 */}
        <Box sx={{ textAlign: 'center', mb: 8 }}>
          <Typography
            variant="h2"
            component="h2"
            sx={{
              fontFamily: 'serif',
              fontWeight: 700,
              color: '#07284b',
              mb: 3,
              fontSize: { xs: '2.5rem', md: '3.5rem' },
              lineHeight: 1.2
            }}
          >
            Our Courses
          </Typography>
          <Typography
            variant="h6"
            sx={{
              color: '#34495e',
              maxWidth: 800,
              mx: 'auto',
              fontSize: '1.2rem',
              lineHeight: 1.6
            }}
          >
            Our courses cover from beginner to advanced levels, targeting children, teenagers, and adults, 
            providing one-on-one, premium small classes, and corporate customized courses.
          </Typography>
        </Box>

        {/* 课程卡片网格 */}
        <Grid container justifyContent="center" spacing={4}>
          {courses.map((course, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Box
                sx={{
                  position: 'relative',
                  '&:hover .course-details-modal': {
                    opacity: 1,
                    visibility: 'visible',
                    transform: 'translateY(0)',
                  }
                }}
              >
                <Card
                  onMouseEnter={() => setHoveredCard(index)}
                  onMouseLeave={() => setHoveredCard(null)}
                  sx={{
                    height: '100%',
                    borderRadius: 3,
                    boxShadow: '0 8px 32px rgba(0,0,0,0.1)',
                    transition: 'all 0.3s ease',
                    cursor: 'pointer',
                    border: '1px solid rgba(255,255,255,0.2)',
                    background: 'rgba(255,255,255,0.9)',
                    backdropFilter: 'blur(10px)',
                    '&:hover': {
                      transform: 'translateY(-8px)',
                      boxShadow: '0 16px 48px rgba(0,0,0,0.15)',
                      '& .course-icon': {
                        transform: 'scale(1.1)',
                      }
                    }
                  }}
                >
                <CardContent sx={{ p: 3, height: '100%', display: 'flex', flexDirection: 'column' }}>
                  {/* 上方内容区域 */}
                  <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
                    {/* 左侧图片区域 */}
                    <Box
                      className="course-icon"
                      sx={{
                        flexShrink: 0,
                        mr: 3,
                        transition: 'transform 0.3s ease'
                      }}
                    >
                      <Box
                        sx={{
                          width: 120,
                          height: 120,
                          borderRadius: '50%',
                          background: `linear-gradient(135deg, ${course.color}15 0%, ${course.color}25 100%)`,
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'center',
                          border: `2px solid ${course.color}20`,
                          overflow: 'hidden'
                        }}
                      >
                        <Box
                          component="img"
                          src={course.image}
                          alt={course.title}
                          sx={{
                            width: '100%',
                            height: '100%',
                            objectFit: 'cover'
                          }}
                        />
                      </Box>
                    </Box>

                    {/* 右侧文字内容 */}
                    <Box sx={{ flexGrow: 1, minWidth: 0 }}>
                      {/* 标题 */}
                      <Typography
                        className="course-title"
                        variant="h6"
                        component="h3"
                        sx={{
                          fontWeight: 700,
                          color: '#2c3e50',
                          mb: 1,
                          transition: 'color 0.3s ease',
                          fontSize: '1.2rem',
                          lineHeight: 1.3
                        }}
                      >
                        {course.title}
                      </Typography>

                      {/* 描述 */}
                      <Typography
                        variant="body2"
                        sx={{
                          color: '#34495e',
                          lineHeight: 1.5,
                          fontSize: '0.9rem',
                          mb: 2
                        }}
                      >
                        {course.description}
                      </Typography>

                      {/* More 文字 */}
                      <Box sx={{ mt: 2 }}>
                        <Typography
                          variant="body2"
                          sx={{
                            color: course.color,
                            fontSize: '0.9rem',
                            fontWeight: 600,
                            cursor: 'pointer',
                            transition: 'all 0.3s ease',
                            '&:hover': {
                              color: course.color,
                              opacity: 0.8,
                              transform: 'translateX(2px)'
                            }
                          }}
                        >
                          More →
                        </Typography>
                      </Box>
                    </Box>
                  </Box>

                  {/* 装饰线条 - 居中显示 */}
                  <Box
                    sx={{
                      width: '60px',
                      height: '3px',
                      background: `linear-gradient(90deg, ${course.color} 0%, ${course.color}80 100%)`,
                      borderRadius: '2px',
                      mx: 'auto',
                      mt: 3
                    }}
                  />
                </CardContent>
              </Card>

              {/* 悬停详情模态框 */}
              <Fade in={hoveredCard === index} timeout={300}>
                <Paper
                  className="course-details-modal"
                  sx={{
                    position: 'absolute',
                    top: '100%',
                    left: 0,
                    right: 0,
                    zIndex: 1000,
                    mt: 2,
                    p: 3,
                    borderRadius: 3,
                    background: `linear-gradient(135deg, 
                      #e7edf9 0%, 
                      #edebef 90%, 
                      #e7edf9 100%)`,
                    backdropFilter: 'blur(20px)',
                    border: '1px solid rgba(7, 40, 75, 0.1)',
                    boxShadow: '0 20px 60px rgba(0,0,0,0.15)',
                    opacity: 0,
                    visibility: 'hidden',
                    transform: 'translateY(-10px)',
                    transition: 'all 0.3s ease',
                    '&::before': {
                      content: '""',
                      position: 'absolute',
                      top: '-8px',
                      left: '50%',
                      transform: 'translateX(-50%)',
                      width: 0,
                      height: 0,
                      borderLeft: '8px solid transparent',
                      borderRight: '8px solid transparent',
                      borderBottom: '8px solid rgba(7, 40, 75, 0.1)',
                    },
                    '&::after': {
                      content: '""',
                      position: 'absolute',
                      top: 0,
                      left: 0,
                      right: 0,
                      bottom: 0,
                      background: 'radial-gradient(circle at 20% 30%, rgba(7, 40, 75, 0.08) 0%, transparent 60%), radial-gradient(circle at 80% 70%, rgba(159, 33, 34, 0.05) 0%, transparent 60%), radial-gradient(circle at 50% 50%, rgba(7, 40, 75, 0.03) 0%, transparent 70%)',
                      borderRadius: 3,
                      pointerEvents: 'none',
                    }
                  }}
                >
                  {/* 价格信息 */}
                  <Box sx={{ mb: 3 }}>
                    <Typography
                      variant="h6"
                      sx={{
                        color: '#2c3e50',
                        fontWeight: 700,
                        mb: 2,
                        textAlign: 'center',
                        fontSize: '1.1rem'
                      }}
                    >
                      Course Pricing
                    </Typography>
                    
                    {/* 主要价格显示 */}
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 3, mb: 2, justifyContent: 'center' }}>
                      {course.pricing.online && (
                        <>
                          <Box sx={{ textAlign: 'center' }}>
                            <Typography
                              variant="caption"
                              sx={{
                                color: '#7f8c8d',
                                fontSize: '0.7rem',
                                display: 'block',
                                mb: 0.5,
                                fontWeight: 600
                              }}
                            >
                              ONLINE
                            </Typography>
                            <Typography
                              variant="h5"
                              sx={{
                                color: '#9f2122',
                                fontWeight: 700,
                                fontSize: '1.4rem',
                                lineHeight: 1
                              }}
                            >
                              {course.pricing.online}
                            </Typography>
                          </Box>
                          
                          <Box sx={{ 
                            width: '2px', 
                            height: '40px', 
                            background: 'linear-gradient(to bottom, transparent, rgba(0,0,0,0.1), transparent)'
                          }} />
                        </>
                      )}
                      
                      <Box sx={{ textAlign: 'center' }}>
                        <Typography
                          variant="caption"
                          sx={{
                            color: '#7f8c8d',
                            fontSize: '0.7rem',
                            display: 'block',
                            mb: 0.5,
                            fontWeight: 600
                          }}
                        >
                          OFFLINE
                        </Typography>
                        <Typography
                          variant="h5"
                          sx={{
                            color: '#9f2122',
                            fontWeight: 700,
                            fontSize: '1.4rem',
                            lineHeight: 1
                          }}
                        >
                          {course.pricing.offline}
                        </Typography>
                      </Box>
                    </Box>

                    {/* 课程详情 */}
                    <Box sx={{ 
                      display: 'flex', 
                      justifyContent: 'space-between',
                      alignItems: 'center',
                      p: 2,
                      bgcolor: 'rgba(0,0,0,0.03)',
                      borderRadius: 2,
                      border: '1px solid rgba(0,0,0,0.08)',
                      mb: 2
                    }}>
                      <Box sx={{ textAlign: 'center', flex: 1 }}>
                        <Typography
                          variant="caption"
                          sx={{
                            color: '#7f8c8d',
                            fontSize: '0.7rem',
                            display: 'block',
                            mb: 0.5,
                            fontWeight: 600
                          }}
                        >
                          DURATION
                        </Typography>
                        <Typography
                          variant="body2"
                          sx={{
                            color: '#2c3e50',
                            fontSize: '0.9rem',
                            fontWeight: 500
                          }}
                        >
                          {course.pricing.duration}
                        </Typography>
                      </Box>
                      <Box sx={{ 
                        width: '1px', 
                        height: '30px', 
                        bgcolor: 'rgba(0,0,0,0.1)' 
                      }} />
                      <Box sx={{ textAlign: 'center', flex: 1 }}>
                        <Typography
                          variant="caption"
                          sx={{
                            color: '#7f8c8d',
                            fontSize: '0.7rem',
                            display: 'block',
                            mb: 0.5,
                            fontWeight: 600
                          }}
                        >
                          GROUP SIZE
                        </Typography>
                        <Typography
                          variant="body2"
                          sx={{
                            color: '#2c3e50',
                            fontSize: '0.9rem',
                            fontWeight: 500
                          }}
                        >
                          {course.pricing.groupSize}
                        </Typography>
                      </Box>
                    </Box>

                    {/* 特殊说明 */}
                    {course.pricing.note && (
                      <Box sx={{
                        p: 2,
                        bgcolor: 'rgba(0,0,0,0.02)',
                        borderRadius: 2,
                        border: '1px solid rgba(0,0,0,0.05)',
                        textAlign: 'center'
                      }}>
                        <Typography
                          variant="body2"
                          sx={{
                            color: '#5a6c7d',
                            fontSize: '0.85rem',
                            fontStyle: 'italic',
                            lineHeight: 1.4
                          }}
                        >
                          {course.pricing.note}
                        </Typography>
                      </Box>
                    )}
                  </Box>
                </Paper>
              </Fade>
            </Box>
            </Grid>
          ))}
        </Grid>

        {/* 底部说明文字 */}
        <Box sx={{ textAlign: 'center', mt: 8 }}>
          <Typography
            variant="body1"
            sx={{
              color: '#34495e',
              fontSize: '1.1rem',
              maxWidth: 600,
              mx: 'auto',
              lineHeight: 1.6,
              mb: 4
            }}
          >
            All courses are designed with flexible scheduling and personalized learning paths 
            to ensure the best learning experience for every student.
          </Typography>

          {/* Call to Action Button */}
          <Button
            variant="contained"
            href="/chihui-mandarin-website/courses"
            sx={{
              bgcolor: '#9f2122',
              color: 'white',
              px: 5,
              py: 2,
              fontSize: '1rem',
              fontWeight: 600,
              borderRadius: 3,
              textTransform: 'none',
              boxShadow: '0 4px 20px rgba(159, 33, 34, 0.3)',
              '&:hover': {
                bgcolor: '#8a1e1f',
                transform: 'translateY(-2px)',
                boxShadow: '0 6px 25px rgba(159, 33, 34, 0.4)'
              },
              transition: 'all 0.3s ease'
            }}
          >
            Contact Us to Learn More
          </Button>
        </Box>
      </Container>
    </Box>
  );
}

export default Courses;
