import React, { useState } from 'react';
import { Box, Container, Typography, Button, Dialog, DialogTitle, DialogContent, IconButton } from '@mui/material';
import { Close } from '@mui/icons-material';
import bgHero1 from '../assets/images/bg-hero-2.webp';
import personHero2 from '../assets/images/person-hero-2.webp';
import ContactForm from './ContactForm';

function Hero() {
  const [contactDialogOpen, setContactDialogOpen] = useState(false);

  return (
    <Box
      sx={{
        backgroundImage: `url(${bgHero1})`,
        backgroundSize: '100% 100%',
        backgroundPosition: 'center bottom',
        backgroundRepeat: 'no-repeat',
        minHeight: '800px',
        display: 'flex',
        alignItems: 'center',
        mt: '100px',
        position: 'relative',
        paddingBottom: '100px',
        overflow: 'hidden'
      }}
    >
      <Container maxWidth="xl" sx={{ position: 'relative', zIndex: 2 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 6, paddingLeft: { xs: 0, md: 2 } }}>
          <Box sx={{ flex: 1, color: 'white' }}>
            <Typography 
              variant="h1" 
              component="h1" 
              sx={{ 
                mb: 3, 
                fontWeight: 700,
                fontSize: { xs: '2.5rem', md: '3.5rem' },
                lineHeight: 1.1
              }}
            >
              LEARN CHINESE WITH WISDOM
            </Typography>
            <Typography 
              variant="h6" 
              sx={{ 
                mb: 4, 
                opacity: 0.95, 
                lineHeight: 1.7,
                fontSize: { xs: '1rem', md: '1.25rem' },
                maxWidth: 500
              }}
            >
              Professional Mandarin teaching team, personalized learning programs, helping you master Mandarin skills easily in Singapore while deeply understanding Chinese culture
            </Typography>
            <Box sx={{ display: 'flex', gap: 2, flexWrap: 'wrap' }}>
              <Button
                variant="contained"
                size="large"
                onClick={() => setContactDialogOpen(true)}
                sx={{
                  bgcolor: 'accent.main',
                  color: 'white',
                  px: 4,
                  py: 1.5,
                  fontSize: '1.1rem',
                  '&:hover': { 
                    bgcolor: 'accent.dark',
                    transform: 'translateY(-2px)',
                    boxShadow: '0 10px 20px rgba(0,0,0,0.2)'
                  },
                  transition: 'all 0.3s ease'
                }}
              >
                Enroll Now
              </Button>
              <Button
                variant="outlined"
                size="large"
                onClick={() => setContactDialogOpen(true)}
                sx={{
                  borderColor: 'accent.main',
                  borderWidth: 2,
                  color: 'accent.light',
                  px: 4,
                  py: 1.5,
                  fontSize: '1.1rem',
                  '&:hover': { 
                    borderColor: 'white', 
                    bgcolor: 'rgba(255,255,255,0.1)',
                    transform: 'translateY(-2px)',
                    boxShadow: '0 10px 20px rgba(0,0,0,0.2)'
                  },
                  transition: 'all 0.3s ease'
                }}
              >
                Free Trial
              </Button>
            </Box>
          </Box>
          
          <Box sx={{ 
            width: '30%',
            }}
          >
            <img src={personHero2} alt="Professional Teacher"  style={{ width: '100%', height: '100%', objectFit: 'cover' }} />
          </Box>
        </Box>
      </Container>

      {/* Contact Form Dialog */}
      <Dialog
        open={contactDialogOpen}
        onClose={() => setContactDialogOpen(false)}
        maxWidth="md"
        fullWidth
        sx={{
          '& .MuiDialog-paper': {
            borderRadius: 4,
            maxHeight: '90vh',
            overflow: 'hidden'
          }
        }}
      >
        <DialogTitle
          sx={{
            background: 'linear-gradient(135deg, #07284b 0%, #9f2122 100%)',
            color: 'white',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            py: 2,
            px: 3
          }}
        >
          <Typography
            component="span"
            sx={{
              fontFamily: 'serif',
              fontWeight: 700,
              fontSize: { xs: '1.3rem', md: '1.5rem' }
            }}
          >
            Contact Us
          </Typography>
          <IconButton
            onClick={() => setContactDialogOpen(false)}
            sx={{
              color: 'white',
              '&:hover': {
                bgcolor: 'rgba(255, 255, 255, 0.1)'
              }
            }}
          >
            <Close />
          </IconButton>
        </DialogTitle>
        <DialogContent
          sx={{
            p: 0,
            '&::-webkit-scrollbar': {
              width: '8px'
            },
            '&::-webkit-scrollbar-track': {
              background: '#f1f1f1'
            },
            '&::-webkit-scrollbar-thumb': {
              background: '#9f2122',
              borderRadius: '4px'
            },
            '&::-webkit-scrollbar-thumb:hover': {
              background: '#8a1e1f'
            }
          }}
        >
          <Box sx={{ px: 4, py: 8 }}>
            <ContactForm />
          </Box>
        </DialogContent>
      </Dialog>
      
    </Box>
  );
}

export default Hero;
