import React from 'react';
import { Box, Container, Typography, Grid, Paper } from '@mui/material';
import features1 from '../assets/images/sample2.webp';
import features2 from '../assets/images/sample1.webp';
import features3 from '../assets/images/sample5.webp';

const features = [
  {
    img: <img src={features1} style={{ width: '100%', height: '100%', objectFit: 'cover' }} alt="Professional Curriculum" />,
    title: 'Professional Curriculum',
    description: 'Systematic Mandarin teaching courses from beginner to advanced, meeting the needs of students at different levels'
  },
  {
    img: <img src={features2} style={{ width: '100%', height: '100%', objectFit: 'cover' }} alt="Cultural Experience" />,
    title: 'Cultural Experience',
    description: 'Deep understanding of Chinese culture through practical activities to enhance language application skills'
  },
  {
    img: <img src={features3} style={{ width: '100%', height: '100%', objectFit: 'cover' }} alt="Expert Teaching Team" />,
    title: 'Expert Teaching Team',
    description: 'Experienced professional teachers with personalized teaching plans to ensure learning effectiveness'
  }
];

function Features() {
  return (
    <Box sx={{ pb: 8, bgcolor: 'white' }}>
      <Container maxWidth="lg">
        <Box sx={{ mb: 6 }}>
          <Typography variant="h3" component="h2" sx={{ mb: 2, fontWeight: 'bold', color: 'primary.main', textAlign: { xs: 'center'} }}>
            Our Features
          </Typography>
          <Typography variant="h6" sx={{ color: 'text.secondary', textAlign: { xs: 'center' } }}>
            Professional Mandarin teaching services, providing the best learning experience for students in Singapore
          </Typography>
        </Box>
        
        <Grid container spacing={4}>
          {features.map((feature, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Paper
                elevation={0}
                sx={{
                  height: '100%',
                  border: '1px solid',
                  borderColor: 'grey.200',
                  borderRadius: 3,
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    transform: 'translateY(-8px)',
                    boxShadow: '0 20px 40px rgba(0,0,0,0.1)',
                    borderColor: 'primary.main'
                  }
                }}
              >
                <Box>
                  {feature.img}
                </Box>
                <Box sx={{ p: 2 }}>
                  <Typography variant="h6" component="h3" sx={{ mb: 1, fontWeight: 'bold', color: 'text.primary' }}>
                    {feature.title}
                  </Typography>
                  <Typography variant="body1" sx={{ color: 'text.secondary', lineHeight: 1.6 }}>
                    {feature.description}
                  </Typography>
                </Box>
              </Paper>
            </Grid>
          ))}
        </Grid>
      </Container>
    </Box>
  );
}

export default Features;
