import React from 'react';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Header from './components/Header';
import Footer from './components/Footer';
import CookieConsent from './components/CookieConsent';
import Home from './pages/Home';
import AboutUs from './pages/AboutUs';
import ContactUs from './pages/ContactUs';
import Courses from './pages/Courses';
import './App.css';

const theme = createTheme({
  palette: {
    primary: {
      main: '#07284b',
      light: '#1e40af',
      dark: '#07284b',
      contrastText: '#ffffff',
      background: '#f5f5f5',
    },
    secondary: {
      main: '#1e40af',
    },
    text: {
      primary: '#333333', 
      secondary: '#666666',
    },
    accent: {
      main: '#9f2122',
      light: '#d93a3b',
      dark: '#6b1415',
      contrastText: '#ffffff',
      background: '#f5f5f5',
    },
  },
  typography: {
    fontFamily: 'Arial, sans-serif',
    h1: {
      fontWeight: 700,
    },
    h2: {
      fontWeight: 700,
    },
    h3: {
      fontWeight: 700,
    },
    h6: {
      fontWeight: 500,
    },
  },
  components: {
    MuiButton: {
      styleOverrides: {
        root: {
          textTransform: 'none',
          borderRadius: 8,
          fontWeight: 600,
        },
        sizeLarge: {
          fontSize: '1.2rem',
        },
      },
    },
    MuiContainer: {
      defaultProps: {
        maxWidth: 'xl',
      },
      styleOverrides: {
        root: {
          '&.MuiContainer-maxWidthXl': {
            maxWidth: '1400px',
          },
        },
      },
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router basename={process.env.PUBLIC_URL}>
        <Routes>
          <Route path="/" element={
            <div className="App">
              <Header />
              <Home />
              <Footer />
              <CookieConsent />
            </div>
          } />
          <Route path="/aboutus" element={
            <div className="App">
              <Header />
              <AboutUs />
              <Footer />
              <CookieConsent />
            </div>
          } />
          <Route path="/courses" element={
            <div className="App">
              <Header />
              <Courses />
              <Footer />
              <CookieConsent />
            </div>
          } />
          <Route path="/contact" element={
            <div className="App">
              <Header />
              <ContactUs />
              <Footer />
              <CookieConsent />
            </div>
          } />
        </Routes>
      </Router>
    </ThemeProvider>
  );
}

export default App;
