import React from 'react';
import { AppBar, Toolbar, Button, Box } from '@mui/material';
import { } from '@mui/icons-material';
import logo from '../assets/images/logo.jpg';

function Header() {

  return (
    <AppBar 
      position="fixed" 
      sx={{ 
        backgroundColor: 'white', 
        color: 'primary.main',
        boxShadow: '0 2px 20px rgba(0,0,0,0.1)',
        backdropFilter: 'blur(10px)'
      }}
    >
      <Toolbar sx={{ maxWidth: 1400, mx: 'auto', width: '100%', px: { xs: 2, md: 4 } }}>
        <Box sx={{ display: 'flex', alignItems: 'center', flexGrow: 1 }}>
          <a href={process.env.PUBLIC_URL + "/"} style={{ textDecoration: 'none', fontSize: '0' }}>
            <img 
              src={logo} 
              alt="CHIHUI Mandarin Logo" 
              style={{ width: 'auto', height: '100px', objectFit: 'cover' }}
            />
          </a>
        </Box>
        <Box sx={{ 
          display: { xs: 'none', md: 'flex' }, 
          gap: 3,
          alignItems: 'center'
        }}>
          <Button 
            color="inherit" 
            href={process.env.PUBLIC_URL + "/#/"}
            size='large'
            sx={{ 
              '&:hover': {
                backgroundColor: 'rgba(37, 99, 235, 0.08)',
                transform: 'translateY(-1px)'
              },
              transition: 'all 0.2s ease'
            }}
          >
            HOME
          </Button>
          <Button 
            color="inherit" 
            href={process.env.PUBLIC_URL + "/#/aboutus"}
            size='large'
            sx={{ 
              '&:hover': {
                backgroundColor: 'rgba(37, 99, 235, 0.08)',
                transform: 'translateY(-1px)'
              },
              transition: 'all 0.2s ease'
            }}
          >
            ABOUT
          </Button>
          <Button 
            color="inherit" 
            href={process.env.PUBLIC_URL + "/#/teachers"}
            size='large'
            sx={{ 
              '&:hover': {
                backgroundColor: 'rgba(37, 99, 235, 0.08)',
                transform: 'translateY(-1px)'
              },
              transition: 'all 0.2s ease'
            }}
          >
            TEACHERS
          </Button>
          <Button 
            color="inherit" 
            href={process.env.PUBLIC_URL + "/#/courses"}
            size='large'
            sx={{ 
              '&:hover': {
                backgroundColor: 'rgba(37, 99, 235, 0.08)',
                transform: 'translateY(-1px)'
              },
              transition: 'all 0.2s ease'
            }}
          >
            COURSES
          </Button>
          <Button 
            color="inherit" 
            href={process.env.PUBLIC_URL + "/#/contact"}
            size='large'
            sx={{ 
              '&:hover': {
                backgroundColor: 'rgba(37, 99, 235, 0.08)',
                transform: 'translateY(-1px)'
              },
              transition: 'all 0.2s ease'
            }}
          >
            CONTACT
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
}

export default Header;
